<script>
import totalInfo from '../../components/Player_totalInfo'
import leagueInfo from '../../components/Player_leagueInfo'
import cupInfo from '../../components/Player_cupInfo'
import nationalInfo from '../../components/Player_nationalInfo'
import qs from "qs";
import echarts from "echarts";

export default {
  data() {
    return {
      active: 0,
      currentTab: 'totalInfo',
      IofoData: {}
    };
  },
  components: { 
    totalInfo,
    leagueInfo,
    cupInfo,
    nationalInfo
  },
  mounted() {
    this.playerInfo();
    // this.playerDetailInfo();
  },
  methods: {
    toggleTab: function(tab) {
      this.currentTab = tab; 
    },
    playerInfo() {
      const data = {
        mem_id: 87969, //目前写死由上个页面传id过来
        _uid: this.Sign.uid,
        _sign: this.Sign.sign
      };
      this.$axios
        .post(
          "http://47.98.97.133/index.php?g=Cliapi&m=Classify&a=teamAllMenInfo",
          qs.stringify(data)
        )
        .then(res => {
          console.log(res.data);
          if (res.data.status == 1) {
            this.IofoData = res.data.data;
          } else {
            console.log(res.data.info);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    playerDetailInfo(){
      const data = {
        team_id: 19,
        mem_id: 87969, //目前写死由上个页面传id过来
        _uid: this.Sign.uid,
        _sign: this.Sign.sign
      };
      this.$axios
        .post(
          "http://47.98.97.133/index.php?g=Cliapi&m=Classify&a=byIdMemInfo",
          qs.stringify(data)
        )
        .then(res => {
          // console.log(res.data);
          // if (res.data.status == 1) {
          //   this.IofoData = res.data.data;
            
          // } else {
          //   console.log(res.data.info);
          // }
        })
        .catch(err => {
          console.log(err);
        });
    },
    drawLine(name, title) {
      let that = this;
      if (name == 2) {
        setTimeout(function() {
          let myChart = that.$echarts.init(that.$refs.Eacharts);
          myChart.setOption({
            title: { text: "" },
            tooltip: {},
            color: ['#ff7d05', '#ED7C30', '#80FF80'],
            legend: {
              data: [],
              textStyle: {
                color: "#fb9005",
                fontSize: 12 ,
                backgroundColor: '#ff7d05',
              }
            },
            radar: {
              name: {
                textStyle: {
                  color: "#fff",
                  backgroundColor: "#ff7d05",
                  borderRadius: 3,
                  padding: [3, 5]
                }
              },
              center: ['50%', '50%'],
              indicator: [
                { name: "速度", max: 100 },
                { name: "力量", max: 100 },
                { name: "防守", max: 100 },
                { name: "盘带", max: 100 },
                { name: "传球", max: 100 },
                { name: "射门", max: 100 }
              ]
            },
            series: [
              {
                type: "radar",
                data: [
                  {
                    value: [75, 81, 83, 78, 76, 66],
                    name: "能力值",
                    areaStyle: {
                      normal: {
                        color: "rgb(175, 229, 255)"
                      }
                    }
                  }
                ]
              }
            ]
          });
        }, 500);
      }
    },
    // 关注
    concern(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'uid' : this.uid,
        'is_cancel' : 0
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=User&a=Subscribe",
         qs.stringify(data)
      ).then(res=>{
        // console.log(res.data);
        // if(res.data.status==0){
        //   this.$toast("关注成功！");
        //   this.status = res.data.status;
        // }
      }).catch(err=>{
        console.log(err);
      })
    },
    // 获取球员基本资料
    

    //返回
    goback(){
      this.$router.back(-1);
    }
  }
};
</script>
