</template>
<script>
import qs from 'qs'
export default {
  data() {
    return {
      active: 0,
      status: '',
      uid:this.Sign.uid,
      sign:this.Sign.sign,
      readHistoryList:[],
      postingCollectList:[],
      postingCount:''
    };
  },
  mounted() {
    this.readHistory();
    this.newsCollect();
    this.videoCollect();
    this.postingCollect();
  },
  methods: {
    postingCollect(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Show&a=favor",
         qs.stringify(data)
      ).then(res=>{
        // console.log(res.data);
        let ret = res.data.data._list;
        this.postingCount = ret.length;        
        for (let i = 0; i < ret.length; i++) {
          let element = ret[i].imgpath;
          ret[i].imgpath = JSON.parse(element);  
        }
        this.postingCollectList = ret;
      }).catch(err=>{
        console.log(err);
      })
    },
    videoCollect(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Show&a=wovideos",
         qs.stringify(data)
      ).then(res=>{
        // console.log(res.data);
        // if(res.data.status==0){
        //   this.status = res.data.status;
        // }
      }).catch(err=>{
        console.log(err);
      })
    },
    newsCollect(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Show&a=zxmation",
         qs.stringify(data)
      ).then(res=>{
        // console.log(res.data);
        // if(res.data.status==0){
        //   this.status = res.data.status;
        // }
      }).catch(err=>{
        console.log(err);
      })
    },
    readHistory(){
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Show&a=wocollect&_uid=${this.uid}&_sign=${this.sign}`
      this.$axios.get(
        urlPath      
      ).then(res=>{
        console.log(res.data);
        let ret = res.data.data._list;
        for (let i = 0; i < ret.length; i++) {
          const element = ret[i].data;
          ret[i].data = JSON.parse(element);     
        }
        this.readHistoryList = ret;
      }).catch(err=>{
        console.log(err);
      })
    },
    goback(){
      this.$router.back(-1);
    }
  },
}
</script>
