    border-radius: 1rem;
    padding: 0.2rem 0;
    margin-top: 0.8rem;
  }

  .operate_content{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 0.3rem;
    /* margin-bottom: 0.3rem; */
    margin-top: 1rem;
  }
  .operate_content img{
    width: 0.9rem;
