<template>
  <div ref="goodsBox">
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback">
      <div class="title"></div>
      <div class="release"></div>
    </header>
    <div class="goods_detail">
      <van-swipe @change="onChange">
        <van-swipe-item v-for="(item, index) of pageData.img" :key="index"><img :src="item" class="swipe1"></van-swipe-item>
        <div class="custom-indicator" slot="indicator">
          {{ current + 1 }}/{{pageData.img.length}}

        </div>
      </van-swipe>
      <div class="title_address">
        <div class="goos_title"><strong>{{pageData.shopname}}</strong></div>
        <div class="place_pay">
          <div class="place_pay_left">
            <img src="../../assets/shopping/gold.png" class="niukou">
            <p class="maohao">:</p>
            <p class="place">{{pageData.jinbi}}</p>
            <p class="baoyou">包邮</p>
          </div>
          <div class="place_pay_right">{{pageData.paymentren}}人付款</div>
        </div>
        <div class="notice">{{pageData.inform.content}}</div>
      </div>
      <div class="color_size" @click="showPopup">
        <div>
          <span v-if="!shop.jinbi">请选择颜色尺码</span>
          <span v-if="shop.jinbi">
            <span v-for="(item,index) of shop.shu" :key="index" style="padding-left:5px;">
              {{item.attribute_name}}:{{item.shuzhi}}
            </span>
            <span style="padding-left:5px;">
              数量:{{shop.count}}
            </span>
          </span>
        </div>
        <img src="../../assets/shopping/right_throw@2x.png" class="right_throw">
      </div>
      <van-popup v-model="show" position="bottom" :style="{ height: '61%' }" closeable>
        <div class="choose_color_size">
          <div class="goods_info">
            <img :src="popupData.img" class="goods_info_img">
            <div class="goods_info_name_place">
              <p class="goods_info_name">{{popupData.shopname}}</p>
              <p class="goods_info_place">金币：<span>{{shop.jinbi}}</span></p>
              <!-- {{shop}} -->
            </div>
          </div>
          <div class="color_info" v-for="(item,index) of popupData.shu" :key="index">
            <div class="color_name">{{item.attribute_name}}</div>
            <div class="color_detail">
              <span v-for="(itemSpace,indexSpace) of item.specification" :class="shop.shu[index].id ==itemSpace.id?'active':''" :key="indexSpace" @click="checkSpecification(index,itemSpace)">
                {{itemSpace.shuzhi}}
              </span>
            </div>
          </div>
          <!-- <div class="color_info">
            <div class="color_name">尺码</div>
            <div class="color_detail">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div> -->
          <div class="num_info">
            <div class="color_name num_title">数量</div>
            <van-stepper v-model="shop.count" />
          </div>
        </div>
        <div class="add_cart add_btn" @click="addHandlerClick()">加入购物车</div>
      </van-popup>
      <div class="size_table">
        <div class="size_table_title">尺码参照</div>
        <!-- <img src="../../assets/shopping/size.png" class="size_img"> -->
        <div v-html="pageData.miaoshu"></div>
      </div>
      <div class="goods_comment">
        <div class="goods_comment_title">商品评论({{productEvaluationData.length}})</div>
        <div class="comment_content">
          <div class="comment_content_one" v-for="(item,index) of productEvaluationData" :key="index">
            <div class="name_img">
              <img :src="item.avatar" class="user_img">
              <div class="comment_user">
                <p class="user_name">{{item.user_nicename}}</p>
                <p class="user_time"><span>{{item.create_time}}</span><span v-for="(itemSub,indexSub) of item.shuxing" :key="index+indexSub">{{itemSub.attribute_name}}：{{itemSub.shuzhi}}</span></p>
              </div>
            </div>
            <div class="comment_detail">
              <div v-html="item.evaluate"></div>
              <img :src="item.eva_pic" v-if="item.eva_pic" style="width:40%;">

            </div>
          </div>
          <div class="look_comment" @click="getProductEvaluation('add')" v-if="productEvaluationData1.length>0">查看全部评论</div>
        </div>
      </div>
      <div class="recommend">
        <div class="recommend_title">为您推荐</div>
        <div class="recommend_goods">
          <van-swipe :loop="false" :width="128" :show-indicators="false">
            <van-swipe-item v-for="(item,index) of pageData.recom" :key="index+item.id" @click="goDetail(item)">
              <div class="recommend_goods_one">
                <img :src="item.img" class="recommend_goods_img">
                <p class="recommend_goods_name">{{item.shopname}}</p>
                <p class="recommend_goods_place">金币：<span>{{item.jinbi}}</span></p>
              </div>
            </van-swipe-item>
          </van-swipe>
        </div>
      </div>
      <div class="kefu_exchange">
        <div class="kefu">
          <img src="../../assets/shopping/kefu.png" class="kefu_img">
          <p class="">客服</p>
        </div>
        <div class="add_cart" @click="addHandlerClick">加入购物车</div>
        <div class="exchange" @click="exchangeHandlerClick">立即兑换</div>
      </div>
      <!-- 上拉菜单加入购物车 -->
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data() {
    return {
      id:'',
      current: 0,
      show: false,
      pageData:{
        img:[],
        recom:[],
        inform:{
          content:""
        }
      },
      popupData:[],
      shop:{
        count:1,
        jinbi:'',
        id:'',
        shu:[],
        type:''
      },
      productEvaluationData:[],
      productEvaluationData1:[],
      pageSize:1,
      pageNum:3
      
    }
  },
  mounted(){
    this.id = this.$route.query.id || '';
  },
  methods: {
    // 返回上一页面
    goback(){
      window.history.back(-1); 
    },
    // 选择规格
    checkSpecification (index,row){
      this.shop.shu[index].id = row.id;
      this.shop.shu[index].shuzhi = row.shuzhi
      let property = []
      for(let item of this.shop.shu){
        if(item.id){
          property.push(item.id)
        }
      }
      this.getPopupPrice(property.join(','))
    },
    goDetail(item){
      this.$router.push({path:'/goods_detail',query:{id:item.id}})
    },
    filteHtml(item){
      var arrEntities={'lt':'<','gt':'>','nbsp':' ','amp':'&','quot':'"'};
      return item.replace(/&(lt|gt|nbsp|amp|quot);/ig,function(all,t){return arrEntities[t];});
    },
    // 获取页面数据
    async getPageData () {
      let data ={
        id: this.id
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=matdetails",qs.stringify(data))
        if(result.data.status==1){
          this.shop={
            count:1,
            jinbi:'',
            id:'',
            shu:[],
            type:''
          }
          this.pageData = result.data.data;
          this.pageData.miaoshu = this.filteHtml(this.pageData.miaoshu)
          this.shop.type = result.data.data.type;
            this.$nextTick(function () {
              var scrolltop = document.documentElement.scrollTop || document.body.scrollTop;
              document.documentElement.scrollTop = document.body.scrollTop =0; 
            })
            
        }
      }catch(err){
        console.log(err);
      }
    },
    // 获取规格
    async getPopupData () {
      let data ={
        goods_id: this.id
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=fication",qs.stringify(data))
        if(result.data.status==1){
          this.popupData = result.data.data
          if(!this.shop.jinbi){
            this.shop.shu = []
            for(let item of this.popupData.shu){
              this.shop.shu.push(
                {
                  id: '',
                  attribute_name: item.attribute_name,
                  cat_id: item.cat_id,
                }
              )
            }
          }
        }
      }catch(err){
        console.log(err);
      }
    },
    async getPopupPrice (property) {
      let data ={
        goods_id: this.id,
        property:property
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=cuts",qs.stringify(data))
        if(result.data.status==1){
          this.shop.jinbi = result.data.data.gold_coin || ''
        }
      }catch(err){
        console.log(err);
      }
    },
    addHandlerClick(){
      if(!this.shop.jinbi){
        this.$toast("请选择规格");
        return false;
      }
      this.addHandler()
    },
    // 立即兑换事件
    exchangeHandlerClick () {
      if(!this.shop.jinbi){
        this.$toast("请选择规格");
        return false;
      }
      this.$toast("无确定订单页面");
    },
    // 商品评价列表
    
    async getProductEvaluation (type = '') {
      if(type == 'add'){
        this.pageNum++
      }
      let data ={
        goods_id: this.id,
        _uid: this.Sign.uid,
        _sign: this.Sign.sign,
        p:this.pageNum,
        r:this.pageSize
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=EvaList",qs.stringify(data))
        if(result.data.status==1){
          this.productEvaluationData = [...this.productEvaluationData,...result.data.data._list];
          this.productEvaluationData1 = result.data.data._list;
        }
      }catch(err){
        console.log(err);
      }
    },
    // 加入购物车
    async addHandler () {
      let property = []
      for(let item of this.shop.shu){
        if(item.id){
          property.push(item.id)
        }
      }
      let data ={
        goods_id: this.id,
        goods_sum:this.shop.count,
        property_id:property.join(','),
        type:1,
        price:this.shop.jinbi,
        _uid: this.Sign.uid,
        _sign: this.Sign.sign,
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Conversion&a=trolley",qs.stringify(data))
        if(result.data.status==1){
            this.$toast(result.data.info);
        }else{
            this.$toast(result.data.info);
        }
      }catch(err){
        console.log(err);
      }
    },
    onChange(index) {
      this.current = index;
    },
    showPopup(){
      this.show = true;
      this.getPopupData()
    },
    
  },
  watch:{
    $route(){
        this.id = this.$route.query.id
      },
      id() {
        this.getPageData();
        this.getProductEvaluation();
      },

  },
  filters:{
    
  }
}
</script>
<style scoped>
.choose_color_size{
  padding-bottom: 2.7rem;
}
header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    font-weight: bold;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
  }
  .release{
    width: 0.5rem;
    height: 0.5rem;
  }
  .swipe1{
    width: 100%;
    position: relative;
  }
  .custom-indicator{
    font-size: 0.9rem;
    width: 3rem;
    background: #E5E5E5;
    padding: 0.2rem 0;
    border-radius: 1rem;
    position: absolute;
    bottom: 1.5rem;
    left: 45%;
  }
  .title_address{
    padding: 0.5rem 0.8rem;
    border-bottom: 1px solid #E6E6E6;
  }
  .goos_title{
    font-size: 0.95rem;
    color: #000000;
    font-family: 微软雅黑;
    letter-spacing: 0.05;
    text-align: left;
  }
  .place_pay{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 0.3rem;
  }
  .place_pay_left{
    display: flex;
    align-items: center;
  }
  .niukou{
    width: 1.5rem;
    
  }
  .maohao{
    font-size: 1.7rem;
    color: #A1A1A1;
    margin-left: 0.5rem;
    margin-right: 0.8rem;
  }
  .place{
    color: #FB4453;
    font-size: 1.5rem;
  }
  .baoyou{
    /* font-size: 0.7rem; */
    -webkit-transform: scale(0.60);
    background: #FC4353;
    color: #FECED2;
    padding: 0.1rem 0.3rem;
    margin-bottom: 0.8rem;
  }
  .place_pay_right{
    /* font-size: 0.8rem; */
    -webkit-transform: scale(0.65);
    color: #BEBEBE;
  }
  .notice{
    font-size: 0.7rem;
    /* width: 100%;
    -webkit-transform: scale(0.60); */
    text-align: left;
    color: #C9C9C9;
    margin-top: 0.3rem;
    line-height: 1.3rem;
  }
  .color_size{
    clear: both;
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
    color: #6D6D6D;
    padding: 0.8rem 0.8rem;
    border-bottom: 1px solid #E0E0E0;
  }
  .right_throw{
    width: 1rem;
    height: 1.2rem;
  }
  .size_table{
    padding: 1rem 0.8rem 0.6rem;
    border-bottom: 0.3rem solid #F5F5F5;
    text-align: left;
  }
  .size_table_title{
    font-size: 0.9rem;
    color: #6D6D6D;
    text-align: left;
    margin-bottom: 0.8rem;
  }
  .size_img{
    width: 100%;
  }
  .goods_comment{
    border-bottom: 0.3rem solid #F5F5F5;
  }
  .goods_comment_title{
    font-size: 0.9rem;
    padding: 0.3rem 0.8rem;
    text-align: left;
    border-bottom: 1px solid #DBDBDB;
  }
  .comment_content_one{
    padding: 0.5rem 0.8rem 0.8rem;
    border-bottom: 1px solid #DBDBDB;
  }
  .name_img{
    display: flex;
  }
  .comment_user{
    margin-left: 0.7rem;
    margin-top: 0.1rem;
  }
  .user_img{
    width: 2.2rem;
    height: 2.2rem;
    border-radius: 50%;
  }
  .user_name{
    font-size: 0.7rem;
    text-align: left;
    color: #0FC2FD;
    margin-bottom: 0.1rem;
  }
  .user_time{
    font-size: 0.7rem;
  }
  .user_time span{
    margin-right: 0.9rem;
    color: #6D6D6D;
  }
  .comment_detail{
    text-align: left;
    font-size: 0.9rem;
    margin-top: 0.6rem;
    margin-left: 0.3rem;
  }
  .look_comment{
    width: 6.7rem;
    font-size: 0.7rem;
    border: 1px solid #FC727E;
    letter-spacing: 0.1rem;
    color: #FD7546;
    margin: 0 auto;
    padding: 0.2rem 0;
    border-radius: 3px;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }
  .recommend_title{
    font-size: 0.9rem;
    padding: 0.3rem 0.8rem;
    text-align: left;
    color: #6A6A6A;
  }
  .recommend_goods{
    display: flex;
    justify-content: center;
    padding-bottom: 12rem;
    /* background: url('../../assets/shopping/beijing1.png'); */
    /* background-size: 100% 100%;
    background-repeat: no-repeat; */
  }
  .recommend_goods_one{
    width: 100%;
    border: 1px solid #CFCFCF;
    margin-right: 0.1rem;
  }
  .recommend_goods_one:last-child{
    margin-right: 0rem;
  }
  .recommend_goods_img{
    width: 7.05rem;
    height: 5rem;
  }
  .recommend_goods_name{
    font-size: 0.7rem;
    margin-bottom: 0.2rem;
    text-align: left;
  }
  .recommend_goods_place{
    font-size: 0.7rem;
    text-align: left;
    margin-bottom: 0.5rem;
  }
  .recommend_goods_place span{
    color: #FC6370;
  }
  .kefu_exchange{
    width: 100%;
    height: 2.7rem;
    display: flex;
    position: fixed;
    right: 0;
    bottom: 0;
    /* position: relative; */
    background: #ffffff;
  }
  .kefu{
    font-size: 0.7rem;
    color: #B8B8B8; 
    position: fixed;
    left: 2rem;
    bottom: 0.2rem; 
  }
  .kefu_img{
    width: 1.3rem;
    height: 1.1rem;
  }
  .add_cart{
    width: 6rem;
    height: 2.7rem;
    background: #333333;
    line-height: 2.7rem;
    color: #F0F0F0;
    font-size: 0.8rem;
    position: fixed;
    right: 6rem;
    bottom: 0;
  }
  .add_btn.add_cart{
    right: 0;
  }
  .exchange{
    width: 6rem;
    height: 2.7rem;
    background: #dc6829;
    line-height: 2.7rem;
    color: #FEFCF9;
    font-size: 0.8rem;
    position: fixed;
    right: 0;
    bottom: 0;
    font-size: 1rem;
  }
  
 
  .goods_info{
    display: flex;
    padding: 1rem 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #F0F0F0;
  }
  .goods_info_name_place{
    width: 13rem;
    font-size: 0.85rem;
    text-align: left;
    padding-top: 0.4rem;
  }
  .goods_info_place{
    margin-top: 0.6rem;
    font-size: 0.95rem;
    color: #757575;
  }
  .goods_info_place span{
    color: #FE6060;
  }
  .goods_info_img{
    width: 5rem;
    height: 5rem;
    margin-right: 1rem;
  }
  .color_info{
    padding: 1rem 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  .color_detail{
    margin-top: 0.6rem;
  }
  .color_detail span{
    padding: .5rem;
    border-radius: 10%;
    background: #D5D5D5;
    margin-right: 1rem;
    margin-bottom: 0.5rem;
    border: 1px solid #D5D5D5
  }
  .color_detail span.active{
    background-color: #e8f7fd;
    border: 1px solid #579ff8;
  }
  .add_cart{
    width: 100%;
    background: #E89B27;
    font-size: 1rem;
    text-align: center;
  }
  .num_info{
    display: flex;
    padding: 1rem 1rem;
    padding-bottom: 1rem;
  }
  .num_title{
    margin-right: 2rem;
  }
</style>


