<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback">
      <div class="title">隐私政策</div>
      <div class="release"></div>
    </header>
    <div class="collect_content collect_content-html" v-html="pagedata.content">
      
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data() {
    return {
      pagedata:''
    };
  },
  mounted() {
   this.getpageData()
  },
  methods: {
    async getpageData(){
      let data = {}
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=privacy",qs.stringify(data))
        
        if(result.data.status==1){
          
          this.pagedata = result.data.data
        }
      }catch(err){
        console.log(err);
      }
    },
    goback(){
      window.history.back(-1);
    }
  },
}
</script>
<style scoped>
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    font-weight: bold;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
  }
  .release{
    width: 0.5rem;
    height: 0.5rem;
  }
  .collect_content{
    padding: 1rem;
    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
  .collect_content p{
    margin-bottom: 1rem;
  }
  .collect_content p.bold{
    font-weight: bold;
    margin-bottom: 0.5rem;
  }

</style>
<style>
.collect_content-html{
    padding: 1rem;
    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
</style>



