                </div>
                <div>
                  <img src="../../assets/home/comment@2x.png" alt="">
                  <span class="info_num">{{items.commentCount}}</span>
                </div>
                <div>
                  <img src="../../assets/home/no_zan@2x.png" alt="">
                  <span class="info_num">{{items.praise}}</span>
                </div>     
              </div>
            </div>
            <div class="recommend_list_one_title">
              <div class="head_title">
                <img src="../../assets/home/4.png" class="head">
                <div class="title_single">
                  <div class="title_single_nickname">
                    <span class="nickname">白浩墨</span>
                    <img src="../../assets/home/logo.png" class="logo">
                  </div>
                  <div class="single_intro">
                    业余作家
                  </div>
                </div>
                <img src="../../assets/home/v1.png" class="v1">               
              </div>    
              <div class="club_info">
                <p class="">从夏天来到了冬天，富力球员踩场训练，备战河北华夏幸福！从夏天来到了冬天，富力球员踩场训练，备战河北华夏幸福！从夏天来到了冬天，富力球员踩场训练，备战河北华夏幸福！</p>
                <div class="author_img">
                  <img src="../../assets/home/expert.jpg" class="img1">
