                  <img src="../../assets/home/expert.jpg" class="img1">
                  <img src="../../assets/home/expert.jpg" class="img1">
                </div>
              </div>
              <div class="operate_content">
                <div>
                  <img src="../../assets/home/share1@2x.png" alt="">
                  <span>分享</span>
                </div>
                <div>
                  <img src="../../assets/home/comment@2x.png" alt="">
                  <span class="info_num">6</span>
                </div>
                <div>
                  <img src="../../assets/home/no_zan@2x.png" alt="">
                  <span class="info_num">4</span>
                </div>     
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      uid:this.Sign.uid,
      sign:this.Sign.sign,
      readHistoryList:[]
    }
  },
  mounted() {

    this.readHistory();
  },
  methods: {
    goback(){
      window.history.back(-1);
    },
    readHistory(){
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Show&a=wocollect&_uid=${this.uid}&_sign=${this.sign}`
      this.$axios.get(
        urlPath      
      ).then(res=>{
        console.log(res.data);
        let ret = res.data.data._list;
        for (let i = 0; i < ret.length; i++) {
          const element = ret[i].data;
          ret[i].data = JSON.parse(element);     
        }
        this.readHistoryList = ret;
      }).catch(err=>{
        console.log(err);
      })
    }
  },
}
</script>
<style scoped>
  header{
