<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback">
      <div class="title"></div>
      <div class="release"></div>
    </header>
    <div class="activity_rules">
      <img src="../../assets/personal/guize.jpg" class="rules_img">
      
    </div>
  </div>
</template>
<script>
export default {
  methods:{
    goback(){
      window.history.back(-1);
    },
  }
}
</script>
<style scoped>
  .rules_img{
    width: 100%;
    height: 100%;
  }
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    font-weight: bold;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
  }
  .release{
    width: 0.5rem;
    height: 0.5rem;
  }
</style>


