<template>
  <div>
    <header>
      <img src="../../assets/personal/fanhui.png" alt="" class="goback" @click="goback()">
      <div class="title">帖子详情</div>
      <div class="reply">26回帖</div>
    </header>
    <div class="news">
        <div class="news_title">{{title}}</div>
        <div class="author_time">
            <div class="author_time_left">
              <span class="news_author">{{author}}</span>
              <span class="news_time">46分钟前</span>
              
            </div>
            <div class="author_time_right">                
            </div>
        </div>
        <div class="news_content">
          <p>{{content}}</p>
          <div v-for="(img,i) in imgs" :key="i" class="posting_img">
            <img :src="img" class="">
          </div> 
        </div>
    </div>
    <!-- 精彩评论 -->
    <div class="good_comment">
      <div class="good_comment_title">
        <p class="small_line"></p>
        <strong class="">精彩评论</strong>
      </div>
      <div class="expert_info">
        <div class="expert_info_one" v-for="(items,i) in commentList" :key="i">
          <div class="expert_info_one_top">
            <div class="head_title">
              <img :src="items.avatar" class="head">
              <div class="title_single">
                <div class="title_single_nickname">{{items.nickname}}</div>
                <div class="fans">40分钟前</div>
              </div>
            </div>
            <div class="concern">
              <p class="zan_num">43</p>
              <img src="../../assets/personal/no_zan@2x.png" class="no_zan">
            </div>
          </div>
          <div class="expert_intro">
            {{items.content}}
          </div> 
        </div>
        <div class="expert_info_one">
          <div class="expert_info_one_top">
            <div class="head_title">
              <img src="../../assets/home/expert.jpg" class="head">
              <div class="title_single">
                <div class="title_single_nickname">蓝黑1986</div>
                <div class="fans">43分钟前</div>
              </div>
            </div>
            <div class="concern">
              <p class="zan_num">25</p>
              <img src="../../assets/personal/zan.png" class="no_zan">
            </div>
          </div>
          <div class="expert_intro">
            前中国足球运动员，出生于山东省青岛市，已退役。中国国家队历史射手榜第一名。参加过2002年韩日世界杯以及1992年、1996年、2004年三届亚洲杯。
          </div> 
        </div>
      </div>
    </div>
    <!-- 发布评论 -->
    <div class="release_comment">
      <input type="text" class="ipt" placeholder="发表你的评论">
      <img src="../../assets/home/dianzan.png" alt="">
      
      <img src="../../assets/home/collection.png" alt="" @click="collect()" v-if="is_favor==0">
      <img src="../../assets/home/collected@2x.png" alt="" v-else-if="is_favor==1" @click="cancelCollect()">
      <img src="../../assets/home/share.png" alt="">
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data(){
    return{
      id:'',
      uid: this.Sign.uid,
      title:'',
      author:'',
      time:'',
      is_favor:'',
      is_praise:'',
      content:'',
      imgs:[],
      commentList:[]
    }
  },
  created() {
    this.id = this.$route.params.id;
    this.loadDetail();
    this.comment();
  },
  methods: {
    // inject:['reload'],
    loadDetail(){
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Zixun&a=zixunDetail&uid=${this.uid}&show_id=${this.id}`
      this.$axios.get(
        urlPath
      ).then(res=>{
        console.log(res.data);
        let ret = res.data.data;
        this.title = ret.title;
        this.author = ret.uname;
        this.content = ret.content;  
        this.time = ret.addtime;
        this.is_favor = ret.is_favor;
        this.is_praise = ret.is_praise;

        for (let i = 0; i < ret.data.length; i++) {
          const element = ret.data;
          ret.data = JSON.parse(element);
          // console.log(ret.data);
          this.imgs = ret.data;  
        }
        // let result = formatDuring(res.data);
        // console.log(result);            
      }).catch(err=>{
        console.log(err);
      })
    },
    // 评论列表
    comment(){
      const uid = this.Sign.uid;
      const sign = this.Sign.sign;
      let urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Show&a=getCommlist&_uid=${uid}&_sign=${sign}&show_id=${this.id}`
      this.$axios.get(
        urlPath
      ).then(res=>{
        console.log(res.data);
        this.commentList = res.data.data;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 收藏
    collect(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'id' : this.id,
        'type' : 'favor'
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Show&a=setShow",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==1){
          this.$toast("收藏成功！");
          // this.reload();
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    // 取消收藏
    cancelCollect(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'id' : this.id
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Show&a=unset_favor",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==1){
          this.$toast("取消收藏成功！");
          // this.reload();
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    goback(){
      this.$router.back(-1);
    },
    // function formatDuring(mss) {
    //   var hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    //   var minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
    //   var seconds = (mss % (1000 * 60)) / 1000;
    //   hours = hours < 10 ? ('0' + hours) : hours;
    //   minutes = minutes < 10 ? ('0' + minutes) : minutes;
    //   seconds = seconds < 10 && seconds >= 1 ? ('0' + seconds) : seconds;
    //   return  hours + " :" + minutes + " :" + seconds;
    // }
  },
}
</script>
<style scoped>
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
    margin-left: 2.5rem;
  }
  .reply{
    font-size: 0.7rem;
    letter-spacing: 0.03rem;
    color: #E4F5FE;
    height: 1rem;
    line-height: 1rem;
    border: 2px solid #7FDEFD;
    padding: 0.15rem 0.3rem;
    margin-top: 1rem;
  }
  .news{
    padding: 0.6rem 0.8rem;
  }
  .news_title{
    font-size: 1.2rem;
    text-align: left;
    letter-spacing: 0.1rem;
    line-height: 2rem;
  }
  .author_time{
    display: flex;
    justify-content: space-between;
    margin-top: 0.7rem;
    margin-bottom: 0.7rem;
  }
  .author_time_left{
    display: flex;
    align-items: center;
  }
  .author_time_right{
    display: flex;
  }
  .news_author{
    color: #01C0FF;
    font-size: 0.8rem;
  }
  .news_time{
    color: #8F9497;
    font-size: 0.8rem;
    margin-left: 0.3rem;
  }
  .renzheng_img{
    width: 1rem;
    height: 1rem;
  }
  .renzheng{
    font-size: 0.7rem;
    color: #9B9B9B;
    margin-left: 0.3rem;
  }
  .news_content{
    text-align: left;
    line-height: 1.7rem;
    letter-spacing: 0.05rem;
    text-indent: 2.1rem;
  }
  .posting_img{
    width: 100%;
    margin-top: 0.4rem;
    margin-left: -2.1rem;
  }
  .posting_img img{
    width: 100%; 
  }
  .good_comment_title{
    font-size: 0.9rem;
    text-align: left;
    display: flex;
    padding: 0.6rem 0.8rem 0.2rem;
  }
  .small_line{
    width: 0.26rem;
    height: 1.1rem;
    background: #01BEFB;
    margin-right: 0.3rem;
    margin-top: 0.1rem;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
  }
  .expert_info_one{
    padding: 0.7rem 0.8rem 0.7rem;
    border-bottom: 2px solid #F1F1F1;
  }
  .expert_info_one_top{
    display: flex;
    justify-content: space-between;
  }
  .head_title{
    display: flex;
  }
  .head{
    width: 2.1rem;
    height: 2.1rem;
    border-radius: 50%;
  }
  .title_single{
    margin-left: 0.3rem;
  }
  .title_single_nickname{
    font-size: 0.8rem;
    color: #01BFFC;
    text-align: left;
  }
  .fans{
    -webkit-transform: scale(0.6);
    color: #AAAAAA;
    text-align: left;
    margin-left: -1rem;
  }
  .concern{
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  .zan_num{
    font-size: 0.7rem;
    color: #A1A1A1;
    margin-right: 0.4rem;
  }
  .no_zan{
    width: 0.85rem;
    height: 0.85rem;
  }
  .expert_intro{
    font-size: 0.9rem;
    color: #000000;
    text-align: left;
    margin-top: 0.3rem;
  }
  .release_comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #E7E7E7;
    padding: 0.8rem 0.8rem;
    
  }
  .ipt{
    background: #ffffff;
    width: 60%;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    border-radius: 0.5rem;
    padding-left: 0.7rem;
  }
  .ipt::placeholder{
    font-size: 0.8rem;
    letter-spacing: 0.08rem;
    color: #C1C1C1;
  }
  .release_comment img{
    width: 1.3rem;
    height: 1.3rem;
  }
</style>


