<template>
    <div>
      <div class="fans_page">
        <div class="fans_list">
          <div class="expert_info" v-for="(items,index) in fansList" :key="index">
            <div class="expert_info_top">
              <div class="head_title">
                <img :src="items.avatar" class="head">
                <div class="title_single">
                  <div class="title_single_nickname">{{items.user_nicename}}</div>
                  <div class="fans">{{items.province}} {{items.city}} 粉丝:{{items.fenCount}}</div>
                </div>
              </div>
              <div class="no_concern" v-if="items.is_guanzhu==0" @click="concern(items.uid)">+ 关注</div>
              <div class="concerns" v-else @click="cancelConcern(items.uid)">已关注</div>
            </div>        
          </div>
          
        </div>
        <div class="all_load"><strong>已经全部加载完毕</strong></div>
      </div>  
    </div>
</template>
<script>
import qs from 'qs'
export default {
  data(){
    return {
      fansList:[]
    }
  },
  mounted() {
    this.loadFans();
  },
  methods: {
    loadFans(){
      const sign = this.Sign.sign;
      const uid = this.Sign.uid;
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Zixun&a=writerFenSi&_uid=${uid}&_sign=${sign}&uid=${uid}&type=2`
      this.$axios.get(
        urlPath
      ).then(res=>{
        console.log(res.data);
        this.fansList = res.data.data; 

      }).catch(err=>{
        console.log(err);
      })
    },
    // 关注
    concern(fansId){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'uid' : fansId,
        'is_cancel' : 0
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=User&a=Subscribe",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==1){
          this.$toast("关注成功！");
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    // 取消关注
    cancelConcern(fansId){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'uid' : fansId,
        'is_cancel' : 1
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=User&a=Subscribe",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==1){
          this.$toast("取消关注成功！");
        }
      }).catch(err=>{
        console.log(err);
      })
    }
  }  
}
</script>
<style scoped>
  .fans_list{
    background: #FAFAFA;
  }
  .concern_list{
    background: #FAFAFA;
  }
  .expert_info{
    padding: 1.1rem 0.8rem 0.7rem;
    border-bottom: 1px solid #F1F1F1;
  }
  .expert_info_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .head_title{
    display: flex;
  }
  .head{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
  }
  .title_single{
    margin-left: 0.8rem;
  }
  .title_single_nickname{
    font-size: 0.8rem;
    color: #000000;
    text-align: left;
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
    letter-spacing: 0.08rem;
  }
  .fans{
    font-size: 0.7rem;
    color: #AAAAAA;
    text-align: left;
  }
  .v1_img{
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 2.2rem;
    bottom: 0;
  }
  .no_concern{
    width: 3.3rem;
    height: 1.3rem;
    border: 1px solid #A2DDF4;
    border-radius: 3px;
    padding: 0.1rem 0.6rem 0;
    font-size: 0.9rem;
    color: #0AC0F6;
  }
  .concerns{
    width: 3.3rem;
    height: 1.3rem;
    border: 1px solid #C3C3C3;
    border-radius: 3px;
    padding: 0.1rem 0.6rem 0;
    font-size: 0.9rem;
    color: #5C5C5C;
  }
  .all_load{
    color: #8D8D8D;
    font-size: 0.8rem;
    letter-spacing: 0.08rem;
    margin-top: 0.7rem;
  }    
</style>


