<template>
    <div>
      
      <div class="page_list" v-for="(items,index) in postingList" :key="index">  
        <router-link :to="{name:'/personal/posting_detail',params:{id:items.id}}" class="release_page"> 
          <div class="expert_info_one">
            <div class="expert_info_one_top">
              <div class="head_title">
                <img src="../assets/home/expert.jpg" class="head">
                <div class="title_single">
                  <div class="title_single_nickname_logo">
                    <span class="title_single_nickname">马赛足球俱乐部</span>
                    <img src="../assets/home/logo.png" class="logo_img">
                  </div>
                  <div class="fans">马赛足球俱乐部官方账号</div>
                </div>
              </div>
              <img src="../assets/home/v1.png" class="v1_img">         
            </div>
            <div class="expert_intro">
              {{items.title}}
              <div v-for="(imgs,i) in items.data" :key="i">
                <img :src="imgs" class="release_img">
              </div>             
            </div> 
          </div>
        </router-link>
        <div class="operate_content">
          <div>
            <img src="../assets/home/share1@2x.png" alt="">
            <span>分享</span>
          </div>
          <div>
            <img src="../assets/home/comment@2x.png" alt="">
            <span v-if="items.comm_sum == 0">评论</span>
            <span v-else>{{items.comm_sum}}</span>
          </div>
          <div>
            <img src="../assets/home/no_zan@2x.png" alt="">
            <span v-if="items.praise == 0">赞</span>
            <span v-else>{{items.praise}}</span>
          </div>     
        </div>
      </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      postingList:[],
      
    }
  },
  created() {
  
  },  
  mounted() {
    this.loadPosting();
    
  },
  methods: {
    loadPosting(){
      const sign = this.Sign.sign;
      const uid = this.Sign.uid;
      console.log(this.Sign.sign);
      console.log(this.Sign.uid);
      const data = {
        // "c_id":20056,
        // 'uid':this.Sign.uid,
        // '_sign':this.Sign.sign       
      },
      headers  = {
            'Content-Type':'application/x-www-form-urlencoded',              
      },
      urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Zixun&a=WriterFaBiao&_uid=${uid}&_sign=${sign}&uid=${uid}`
      this.$axios.get(
        urlPath
      ).then(res=>{
        console.log(res.data.data);
        let ret = res.data.data._list;
        for (let i = 0; i < ret.length; i++) {
          const element = ret[i].data;
          ret[i].data = JSON.parse(element);       
        }
        this.postingList = ret;
      }).catch(err=>{
        console.log(err);
      })
    }    
  },  
}
</script>
<style scoped>
  .page_list{
    background: #FAFAFA;
    text-align: left;
  }
  .expert_info_one{
    padding: 0.8rem 0.8rem 0.7rem;
  }
  .expert_info_one_top{
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  .head_title{
    display: flex;
    
  }
  .head{
    width: 2.7rem;
    height: 2.7rem;
    border-radius: 50%;
  }
  .title_single{
    margin-left: 0.3rem;
  }
  .title_single_nickname_logo{
    text-align: left;
  }
  .title_single_nickname{
    font-size: 0.7rem;
    color: #000000;
    text-align: left;
    letter-spacing: 0.05rem;
  }
  .logo_img{
    width: 1rem;
    height: 1rem;
    
  }
  .v2_img{
    width: 1rem;
    height: 0.7rem;
  }
  .fans{
    font-size: 0.7rem;
    color: #AAAAAA;
    text-align: left;
    letter-spacing: 0.05rem;
    margin-top: 0.1rem;
  }
  .v1_img{
    width: 1rem;
    height: 1rem;
    position: absolute;
    left: 1.7rem;
    bottom: 0;
  }
  .expert_intro{
    font-size: 1.1rem;
    color: #777777;
    text-align: left;
    margin-top: 0.3rem;
    letter-spacing: 0.05rem;
  }
  .release_img{
    width: 73%;
    margin-top: 0.5rem;
  }
  .operate_content{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 0.6rem;
    border-bottom: 0.3rem solid #ECECEC;
  }
  .operate_content img{
    width: 0.9rem;
    height: 0.9rem;
  }
  .operate_content span{
    font-size: 0.7rem;
  }  
</style>


