<template>
    <div>
      <div class="comment_page">
        <div class="comment_list">
          <div class="recommend_list_one" v-for="(items,i) in commentList" :key="i">
            <div class="recommend_list_one_title">
              <div class="head_title">
                <img :src="items.avatar" class="head">
                <div class="title_single">
                  <div class="title_single_nickname_logo">
                    <span class="title_single_nickname">{{items.nickname}}</span>
                    <img src="../assets/home/v2.png" class="v2_img">
                  </div>
                  
                  <div class="single_intro">
                    {{items.create_time}} · 回复文章
                  </div>
                </div>
              </div>    
              <div class="team_name">{{items.content}}</div>
              <div class="season">
                <div class="season_title">[原文]{{items.show_title}}</div>
                <div class="right_row"></div>
              </div>
              <div class="time_zuan">
                <p class="time">{{items.showtype}}</p>
                <p class="zuan">{{items.praiseCount}}个赞</p>
              </div>
            </div>
          </div>
        </div>
        <div class="all_load" @click="comment_num()"><strong>已经全部加载完毕</strong></div>
      </div>  
    </div>
</template>
<script>
import qs from 'qs'
export default {
  data(){
    return {
      uid: this.Sign.uid,
      sign: this.Sign.sign,
      commentList:[],
      commentListCount:''
    }
  },
  mounted() {
    this.loadComment(); 
    this.comment_num();
  },
  methods: {
    loadComment(){
      const data={
        '_uid': this.uid,
        '_sign': this.sign,
        'uid': this.uid
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Zixun&a=writeComment",
        qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        this.commentList = res.data.data._list;
        this.commentListCount = res.data.data._list.length;
        console.log(this.commentListCount);
      }).catch(err=>{
        console.log(err);
      })
    },
    // 父子传参
    comment_num(){
      this.$emit('childtovalue',this.commentListCount);
    }
  }
}
</script>
<style scoped>
  .comment_list{
    background: #FAFAFA;
    border-bottom: 0.4rem solid #F1F1F1;
  }
  .recommend_list_one{
    padding: 1rem 0.8rem 0.5rem;
    
  }
  .head_title{
    display: flex;
  }
  .head{
    width: 2.3rem;
    height: 2.3rem;
    border-radius: 50%;
  }
  .title_single{
    margin-left: 0.8rem;
  }
  .title_single_nickname_logo{
    text-align: left;
  }
  .title_single_nickname{
    font-size: 0.8rem;
    color: #000000;
    text-align: left;
    margin-bottom: 0.2rem;
  }
  .v2_img{
    width: 0.8rem;
    height: 0.7rem;
  }
  .single_intro{
    font-size: 0.7rem;
    color: #959595;
  }
  .single_intro span:first-child{
    background: #FEE8EA;
    border-radius: 0.8rem;
    padding: 0 0.4rem;
    margin-right: 0.2rem;
  }
  .single_intro span:nth-child(2){
    background: #E8F1FF;
    color: #347EF7;
    border-radius: 0.8rem;
    padding: 0 0.4rem;
    margin-right: 0.2rem;
  }
  .single_intro span:nth-child(3){
    background: #FEE8EA;
    border-radius: 0.8rem;
    padding: 0 0.4rem;
  }
  .team_name{
    margin-top: 0.7rem;
    text-align: left;
  }
  .season{
    background: #F4F4F4;
    display: flex;
    justify-content: space-between;
    padding: 0.3rem 0.7rem;
    margin-top: 1rem;
  }
  .season_title{
    font-size: 0.7rem;
    color: #A2A2A2;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .right_row{
    color: #A2A2A2;
  }
  .time_zuan{
    display: flex;
    justify-content: space-between;
    font-size: 0.7rem;
    margin-top: 0.7rem;

  }
  .time{
    color: #A1A1A1;
  }
  .zuan{
    color: #A1A1A1;
    -webkit-transform: scale(0.8);
  }
  .all_load{
    font-size: 0.8rem;
    color: #7F7F7F;
    letter-spacing: 0.05rem;
    margin-top: 0.6rem;
  }  
</style>


