<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback"  @click="goback"> 
      <div class="title">5G体育</div>
      <div class="release">下一步</div>
    </header>
    <div class="football_field">
      <div class="tackle">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
      </div>
      <div class="midfield">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
      </div>
      <div class="full_back">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
        <img src="../../assets/home/tianjia.png" class="add_img">
      </div>
      <div class="goalkeeper">
        <img src="../../assets/home/tianjia.png" class="add_img">
      </div>
      <div class="choose_player">点击球队，挑选队员</div>
      <div class="kong"></div>
    </div>  
    <div class="head_coach">
      <div class="kuang"></div>
      <div class="head_coach_info">
        <p>主教练：小雨124</p>
        <p>创建时间：小雨124</p>
        <p>技术支持：5G体育</p>
      </div>
    </div>  
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 2
    };
  },
  methods:{
    goback(){
      window.history.back(-1);
    }
  }
}
</script>
<style scoped>
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.05rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
    font-weight: 100;
    margin-left: 1.5rem;
  }
  .release{
    width:2.4rem;
    height: 0.5rem;
    /* border: 1px solid red; */
    font-size: 0.7rem;
    color: #D0F3FD;
    letter-spacing: 0.05rem;
  }
  .football_field{
    background: url('../../assets/home/beijing2.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  .add_img{
    width: 1.9rem;
    height: 1.9rem;
  }
  .tackle .add_img:first-child{
    margin-right: 3rem;
  }
  .tackle{
    margin-bottom: 3.5rem;
    padding-top: 2.6rem;
  }
  .midfield{
    margin-bottom: 3.5rem;
  }
  .midfield .add_img{
    margin-right: 3rem;
  }
  .midfield .add_img:last-child{
    margin-right: 0;
  }
  .full_back{
    margin-bottom: 3.5rem;
  }
  .full_back .add_img{
    margin-right: 3rem;
  }
  .full_back .add_img:last-child{
    margin-right: 0;
  }
  .goalkeeper{
    padding-bottom: 0.5rem;
  }
  .choose_player{
    font-size: 0.7rem;
    width: 9rem;
    background: #3D7544;
    border-radius: 1rem;
    padding: 0.3rem 0;
    color: #ECF2ED;
    margin: 0 auto;
  }
  .kong{
    height: 0.6rem;
  }
  .head_coach{
    background: #023344;
    display: flex;
    justify-content: center;
    padding: 4rem 0 4rem;
  }
  .kuang{
    width: 4.7rem;
    height: 4.7rem;
    background: #ffffff;
    margin-right: 1.5rem;
  }
  .head_coach_info{
    color: #ffffff;
    font-size: 0.8rem;

  }
  .head_coach_info p{
    margin-bottom: 0.8rem;
  }
</style>


