<template>
  <div>
    <div class="info_switch">
      
      <div class="bottom_list">
        <div class="left_title">
          <p class="checked4">射手榜</p>
          <p>助攻榜</p>
          <p>黄牌</p>
          <p>红牌</p>
          <p>射门</p>
          <p>射正</p>
          <p>点球</p>
          <p>击中门框</p>
          <p>尝试过人</p>
          <p>成功过人</p>
          <p>越位</p>
          <p>被犯规</p>
          <p>传球</p>
        </div>
        <div class="right_list">
          <div class="right_list_title">
            <div class="list_title_player">球员</div>
            <div class="list_title_team">球队</div>
            <div class="">进球(点球)</div>
          </div>
          <div class="list_data_one" v-for="(items,index) in shooterList" :key="index">
            <div class="list_data_one_left">
              <p>{{index+1}}</p>
              <img src="../assets/home/expert.jpg" class="player_img">
              <p>{{items.playerNameChs}}</p>
            </div>
            <p class="team_name">{{items.teamNameChs}}</p>
            <p class="goal_num">{{items.goals}}({{items.homePenalty+items.awayPenalty}})</p>
          </div>
          <!-- <div class="list_data_one">
            <div class="list_data_one_left">
              <p>2</p>
              <img src="../../assets/home/expert.jpg" class="player_img">
              <p>特谢拉</p>
            </div>
            <p class="team_name">江苏苏宁</p>
            <p class="goal_num">16(4)</p>
          </div>
          <div class="list_data_one">
            <div class="list_data_one_left">
              <p>3</p>
              <img src="../../assets/home/expert.jpg" class="player_img">
              <p>佩莱</p>
            </div>
            <p class="team_name">山东鲁能泰山</p>
            <p class="goal_num">15(4)</p>
          </div> -->

        </div>
      </div>
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data() {
    return {
      active: 0,
      shooterList: []
    };
  },
  mounted() {
    this.shooterInfo();
  },
  methods: {
    shooterInfo(){
      const data = {
        _uid: this.Sign.uid,
        _sign: this.Sign.sign,
        leagueId : 36
      };
      this.$axios
        .post(
          "http://47.98.97.133/index.php?g=Cliapi&m=DataList&a=topScorerBang",
          qs.stringify(data)
        )
        .then(res => {
          // console.log(res.data);
          this.shooterList = res.data.data.list;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
}
</script>
<style scoped>
  header{
    display: flex;
    background: #00BFFE;
    justify-content: center;
    padding: 2.2rem 0 0.5rem;
    color: #F7FBF9;
  }
  .top_nav{
    margin-right: 1.5rem;
  }
  .top_nav:last-child{
    margin-right: 0;
  }
  .checked{
    color: #FBFDFD;
    border-bottom: 2px solid #ffffff;
    padding-bottom: 0.2rem;
  }
  .match_nav{
    display: flex;
    justify-content: space-between;
    padding: 0.6rem 1rem;
    align-items: center;
    border-bottom: 0.5rem solid #F7F7F7;
  }
  .match_nav_left{
    display: flex;
    font-size: 0.8rem; 
  }
  .match_nav1{
    margin-right: 1.8rem;
    letter-spacing: 0.1rem;
  }
  .checked1{
    color: #24BDF9;
    border-bottom: 2px solid #01BEFD;
    
  }
  .match_nav_right{
    display: flex;
    align-items: center;
    font-size: 0.7rem;
    color: #A0A0A0;
  }
  .set_up{
    width: 1rem;
    height: 1rem;
    margin-right: 0.2rem;   
  }
  .national_league{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.7rem;
    padding: 0.5rem 1rem;
  }
  .drop_down{
    width: 1rem;
    height: 0.8rem;
    margin-top: 0.2rem;
  }
  .checked2{
    color: #36C4F7;
    font-size: 0.8rem;
    border-bottom: 2px solid #00BFFC;
    padding-bottom: 0.2rem;
  }
  .info_switch{
    background: #333D46;
  }
  .year_team_player{
    padding: 0.2rem 0.8rem 0.2rem 0.5em;
    /* padding-right: 0.8rem; */
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .year_info{
    display: flex;
    align-items: center;
    font-size: 0.8rem;
  }
  .year_info_time{
    color: #89939E;
  }
  .small_down{
    width: 0.5rem;
    height: 0.5rem;
    background: #939BAE;
    border-radius: 50%;
    margin-left: 0.2rem;
  }
  .top_title{
    display: flex;
    border: 1px solid #1B232A;
    margin: 0.3rem 0;
    color: #939CA9;
  }
  .top_title_one{
    border-right: 1px solid #1C2125;
    font-size: 0.7rem;
    padding: 0.3rem 1.1rem;
  }
  .top_title_one:last-child{
    border-right: none;
  }
  .checked3{
    background: #1D2228;
    color: #F5F6F8;
  }
  .bottom_list{
    width: 100%;
    display: flex;
  }
  .left_title{
    width: 21%;
    background: #272E36;
    color: #9098A5;
    font-size: 0.7rem;
    letter-spacing: 0.05rem;
  }
  .left_title p{
    padding: 1rem 0;
  }
  .checked4{
    background: #00BFFE;
    color: #DAF5F7;
  }
  .right_list{
    width: 79%; 
  }
  .right_list_title{
    display: flex;
    justify-content: space-between;
    color: #C4C7CC;
    background: #3C4650;
    font-size: 0.7rem;
    padding: 0.5rem 0.6rem 0.5rem 1.5rem;
  }
  .list_title_player{
    margin-left: 0.2rem;
  }
  .list_title_team{
    margin-left: 3rem;
  }
  .list_data_one{
    display: flex;
    align-items: center;
    padding: 0.5rem 0.7rem;
    justify-content: space-between;
    font-size: 0.7rem;
    color: #C8CCCE;
    border-bottom: 1px solid #2F3841;
  }
  .list_data_one_left{
    display: flex;
    align-items: center;
  }
  .player_img{
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 50%;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  .team_name{
    margin-right: 1rem;
  }
  .goal_num{
    margin-right: 0.7rem;
  }
</style>


