// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import sign from './api/sign.js'
import moment from 'moment'

Vue.config.productionTip = false
Vue.prototype.$axios = axios;//把axios挂载到vue上
// Vue.prototype.baseUrl = 'http://47.98.97.133'
Vue.prototype.Sign = sign
Vue.prototype.$moment = moment;
moment.locale('zh-cn');//需要汉化

import { Tab, Tabs,Switch,Swipe, SwipeItem,ActionSheet,Toast,Popup,Stepper } from 'vant';
Vue.use(Tab).use(Tabs).use(Switch).use(Swipe).use(SwipeItem).use(ActionSheet).use(Toast).use(Popup).use(Stepper);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



