<template>
  <div>
    <header>
      <div class="goback_more">
        <img src="../../assets/home/fanhui.png" class="fanhui" @click="fanhui">
        <img src="../../assets/home/more@2x.png" class="more">
      </div>
      <div class="head_concern">
        <div class="head">
          <img :src="logo" class="club_logo">
        </div>
        <img src="../../assets/home/v1.png" class="v1">
        <div class="concern" @click="concern()" v-show="_uid!=uid" v-if="status==1">+ 关注</div>
        <div class="concern" @click="cancelConcern()" v-show="_uid!=uid" v-else>已关注</div>
      </div>
      <div class="club_info">
        <div class="name_logo">
          <span class="club_name">{{nickname}}</span>
          <img src="../../assets/home/logo.png" class="logo">
        </div>   
        <div class="renzheng_account">
          <p class="renzheng">已认证</p>
          <p>马赛足球俱乐部官方账号</p>
        </div>
        <div class="club_fan">
          <img src="../../assets/home/flag.png" class="flag">
          <p class="masai_fan">{{qiuMi}}</p>
        </div>
        <div class="user_info">
          来自{{province}} {{city}} 已成为懂球帝<span>{{days}}</span>天 &nbsp;获得<span>{{zan}}</span>个赞
        </div>
      </div>
    </header>
    <div class="club_content">
      <div class="club_part">
        <ul class="ranking_name">
          <li @click="toggleTab('publish')">
            <p>发表</p>
            <p class="club_content_num">11</p>
          </li>
          <li @click="toggleTab('comment')">
            <p>评论</p>
            <p class="club_content_num">{{comment_num}}</p>
          </li>
          <li @click="toggleTab('concern')">
            <p>关注</p>
            <p class="club_content_num">2</p>
          </li>
          <li @click="toggleTab('fans')">
            <p>粉丝</p>
            <p class="club_content_num">3092</p>
          </li>
        </ul>
      </div>      
<!-- 发表页面 -->
      <publish :is="currentTab" keep-alive v-on:childtovalue="childfromvalue"></publish>
<!-- 评论页面 -->
      
<!-- 关注页面 -->
      
<!-- 粉丝页面 -->
      
    </div>
  </div>
</template>
<script>
import publish from '../../components/Publish';
import comment from '../../components/Comment';
import concern from '../../components/Concern';
import fans from '../../components/Fans';
import qs from 'qs'
export default {
  data() {
    return {
      currentTab: 'publish',
      _uid: this.Sign.uid,
      sign: this.Sign.sign,
      uid: 27538,
      comment_num:'',
      logo:'',
      nickname:'',
      days:'',
      bg_img:'',
      zan:'',
      province:'',
      city:'',
      qiuMi:'',
      status:''
    }
  },
  components: { // 声明子组件
    publish,
    comment,
    concern,
    fans
  },
  created() {
    
  },  
  mounted() {
    this.authorInfo();   
  },
  methods: {
    fanhui(){
      this.$router.go(-1);
    },
    toggleTab: function(tab) {
      this.currentTab = tab; // tab 为当前触发标签页的组件名
    },
    authorInfo(){
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Zixun&a=ZixunWriter&_uid=${this._uid}&_sign=${this.sign}&uid=${this.uid}`
      this.$axios.get(
        urlPath
      ).then(res=>{
        console.log(res.data.data);
        let ret = res.data.data;
        this.logo = ret.avatar;
        this.bg_img = ret.background_img;
        this.nickname = ret.user_nicename;
        this.days = ret.days;
        this.zan = ret.priase;
        this.province = ret.province;
        this.city = ret.city;
        this.qiuMi = ret.qiuMi;
      }).catch(err=>{
        console.log(err);
      })
    },
    // 关注
    concern(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'uid' : this.uid,
        'is_cancel' : 0
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=User&a=Subscribe",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==0){
          this.$toast("关注成功！");
          this.status = res.data.status;
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    // 取消关注
    cancelConcern(){
      const data = {
        '_uid' : this.Sign.uid,
        '_sign' : this.Sign.sign,
        'uid' : 27538,
        'is_cancel' : 1
      }
      this.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=User&a=Subscribe",
         qs.stringify(data)
      ).then(res=>{
        console.log(res.data);
        if(res.data.status==1){
          this.$toast("取消关注成功！");
          this.status = res.data.status;
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    // 父子传参 
    childfromvalue: function (commentListCount) {
      this.comment_num = commentListCount;
      console.log(this.comment_num);
    }
    
  },
  watch:{
    comment_num(val){
      console.log(val);
      
    }
  }
}
</script>
<style scoped>
  header{
    background-image: url('../../assets/home/beijing.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    padding: 0.8rem 0.7rem 0.4rem;
  }
  .goback_more{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2rem;
  }
  .fanhui{
    width: 0.7rem;
    height: 1rem;
  }
  .more{
    width: 1rem;
    height: 0.2rem;
  }
  .head_concern{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    position: relative;
  }
  .head{
    margin-top: 0.8rem;
  }
  .club_logo{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
  }
  .v1{
    width: 1rem;
    height: 1rem;
    position: absolute;
    bottom: 0.3rem;
    left: 2.7rem;
  }
  .concern{
    width: 5.5rem;
    height: 2rem;
    background: #02AAEB;
    font-size: 0.95rem;
    color: #FCF9F6;
    text-align: center;
    line-height: 2rem;
    border-radius: 3px;
  }
  .name_logo{
    display: flex;
    align-items: center;
    margin-top: 0.2rem;
  }
  .club_name{
    font-size: 1rem;
    color: #ffffff;
    text-align: left;
    letter-spacing: 0.1rem;
    margin-right: 0.3rem;
  }
  .logo{
    width: 1rem;
    height: 1rem;
    margin-top: 0.2rem;
  }
  .renzheng_account{
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    color: #EFF4F3;
    margin-top: 0.5rem;
  }
  .renzheng{
    font-size: 0.7rem;
    color: #F6F6F7;
    background: #34442C;
    height: 1rem;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
    border: 1px solid #D4D6D7;
    border-radius: 3px;
    margin-right: 0.5rem;
  }
  .masai_fan{
    font-size: 0.8rem;
    color: #E8ECED;
    font-weight: 100;
    text-align: left;
    margin-left: 0.3rem;
  }
  .flag{
    width: 1rem;
    height: 1rem;
  }
  .club_fan{
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
  }
  .user_info{
    font-size: 0.9rem;
    color: #F5F9F9;
    text-align: left;
    margin-top: 0.4rem;
  }
  .user_info span{
    color: #059EC8;
  }

  .ranking_name{
    font-size: 0.9rem;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #EBEBEB;
    letter-spacing: 0.05rem;
  }
  .ranking_name li{
    color: #000000;
    width: 5rem;
    text-align: center;
    margin-left: 0.4rem;
    padding-top: 0.6rem;
    padding-bottom: 0.4rem;
  }
  .ranking_name li:first-child{
    color: #27C4F4;
    border-bottom: 2px solid #01BFFC;
    font-weight: 700;
  }
  .club_part{
    background: #FAFAFA;
  }
  .club_content_num{
    font-size: 0.7rem;
    font-weight: 100;
    margin-top: 0.3rem;
  }
/* 发表页面 */
  
/* 评论页面 */
  
/* 关注页面 */
  
/* 粉丝页面 */
  
</style>


