<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" class="goback" @click="goback()"/>
      <div class="title">专家主页</div>
      <div class="release"></div>
    </header>
    <div class="expert_info">
      <div class="expert_info_top">
        <div class="head_title">
          <img :src="img" class="head" />
          <div class="title_single">
            <h4 class="title_single_nickname">{{name}}</h4>
            <div class="fans">{{fans}} 粉丝</div>
          </div>
        </div>
        <div class="concern" v-if="is_zhu==0" @click="concern()">
          +
          <strong>关注</strong>
        </div>
        <div class="concern" v-else-if="is_zhu==1||status==1" @click="concern()">
          <strong>已关注</strong>
        </div>
      </div>
      <div class="single_intro">
        <span>意甲2连红</span>
        <span>欧冠3连红</span>
        <span>瑞典超2连红</span>
        <!-- <span>法乙2连红</span>
        <span>单关3连红</span>-->
      </div>
      <div class="expert_intro">{{introduce}}</div>
    </div>
    <!-- 近期战绩 -->
    <div class="recent_achievements">
      <ul class="type_name">
        <li>单关</li>
        <li>串关</li>
        <li>大小球</li>
        <li>让球</li>
      </ul>
      <div class="type_trend">
        <p class="trend_title">近期战绩走势</p>
        <select name class="all_match">
          <option value="1">全部赛事</option>
          <option value="2">赛事1</option>
          <option value="3">赛事2</option>
        </select>
      </div>
      <!-- 走势图 -->
      <div class="trend_chart">
        <img src="../../assets/home/trend.png" class="trend_img" />
      </div>
    </div>
    <!-- 在售方案 -->
    <div class="onsale_scheme">
      <div class="scheme_title">
        <div class="scheme_title_left">
          <h5>在售方案</h5>
        </div>
        <a class="scheme_title_right"></a>
      </div>
      <div class="scheme_detail" v-for="(items,index) in plan" :key="index">
        <h4 class="team_name">{{items.title}}</h4>
        <div class="season">
          <div
            class="season_title"
          >{{items.matchtime}} {{items.league}} | {{items.home}} vs {{items.away}}</div>
          <div class="right_row">></div>
        </div>
        <div class="time_zuan">
          <p class="time">1天前发布</p>
          <p class="zuan">竞足 | {{items.price}}红钻</p>
        </div>
      </div>
      <!-- <div class="scheme_detail">
        <h4 class="team_name">欧罗巴信心推荐，布拉迪斯迎战狼队带你红单</h4>
        <div class="season">
          <div class="season_title">10/25 00:23 欧罗巴杯 | 布拉迪斯 vs 狼队</div>
          <div class="right_row">></div>
        </div>
        <div class="time_zuan">
          <p class="time">1天前发布</p>
          <p class="zuan">竞足 | 88红钻</p>
        </div>
      </div>-->
    </div>
    <!-- 历史方案 -->
    <div class="history_scheme">
      <div class="scheme_title">
        <div class="scheme_title_left">
          <h5>历史方案</h5>
        </div>
        <a class="scheme_title_right"></a>
      </div>
      <div class="scheme_detail" v-for="(items,i) in historyPlanList" :key="i">
        <h4 class="team_name">{{items.title}}</h4>
        <div class="season">
          <div
            class="season_title"
          >{{items.matchtime}} {{items.league}} | {{items.home}} vs {{items.away}}</div>
          <div class="right_row">></div>
        </div>
        <div class="time_zuan">
          <p class="time">竞足 | 查看</p>
          <p class="red" v-if="items.affirm==1">红</p>
          <p class="black" v-else>黑</p>
        </div>
      </div>
      <!-- <div class="scheme_detail">
        <h4 class="team_name">昨日热刺让胜荐中，本场莱红牛主场迎战泽尼特带你红单【双选】</h4>
        <div class="season">
          <div class="season_title">10/25 00:23 欧冠 | 莱红牛 vs 泽尼特</div>
          <div class="right_row">></div>
        </div>
        <div class="time_zuan">
          <p class="time">竞足 | 查看</p>
          <p class="black">黑</p>
        </div>
      </div>-->
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data() {
    return {
      id: "",
      uid: "",
      sign: "",
      name: "",
      img: "",
      fans: "",
      introduce: "",
      is_zhu: "",
      status: "",
      plan: [],
      matchtime: "",
      create_time: "",
      price: "",
      historyPlanList: []
    };
  },
  mounted() {
    this.id = this.$route.params.id;
    this.uid = this.Sign.uid;
    this.sign = this.Sign.sign;
    console.log(this.id);
    this.expertDetail();
    this.historyPlan();
  },
  inject: ['reload'],
  methods: {
    expertDetail() {
      const urlPath =
        `http://47.98.97.133/index.php?g=Cliapi&m=Industry&a=detailescte&user_id=27539&spec_type=${this.id}`                                                                           
      this.$axios.get(
        urlPath
      ).then(res => {
        console.log(res.data);
        let ret = res.data.data;
        this.name = ret.speciname;
        this.img = ret.img;
        this.fans = ret.fans;
        this.introduce = ret.introduce;
        this.is_zhu = ret.is_zhu;
        this.plan = ret.sale;
      })
      .catch(err => {
        console.log(err);
      });
    },
    historyPlan() {
      const urlPath = `http://47.98.97.133/index.php?g=Cliapi&m=Industry&a=quhistory&speci_id=${this.id}`;
      this.$axios
        .get(urlPath)
        .then(res => {
          // console.log(res.data);
          this.historyPlanList = res.data.data._list;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 关注专家
    concern() {
      let that = this;
      const data = {
          '_uid': that.uid,
          '_sign': that.sign,
          'spe_id': that.id
      }
      
      that.$axios.post(
        "http://47.98.97.133/index.php?g=Cliapi&m=Specialist&a=offatten",
        qs.stringify(data)
      )
      .then(res => {
        console.log(res.data);
        that.status = res.data.status;
        if(res.data.status==0){
          that.$toast("取消关注成功！");
          // that.reload();
        }
      }).catch(err => {
        console.log(err);
      });
    },
    goback() {
      this.$router.back(-1);
    }
  }
};
</script>
<style scoped>
header {
  background: #00bffe;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  /* border: 1px solid #f00; */
  height: 2.4rem;
  line-height: 3.4rem;
  color: #ffffff;
}
.goback {
  width: 0.5rem;
  height: 1rem;
  color: #d8f2fe;
  padding-top: 1.2rem;
}
.title {
  font-size: 1.1rem;
  letter-spacing: 0.2rem;
  color: #edfafe;
  font-family: "微软雅黑";
  /* font-weight: 550; */
}
.release {
  width: 0.5rem;
  height: 0.5rem;
}
.expert_info {
  padding: 1.1rem 0.8rem 0.7rem;
  border-bottom: 0.5rem solid #f1f1f1;
}
.expert_info_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.head_title {
  display: flex;
}
.head {
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
}
.title_single {
  margin-left: 0.8rem;
}
.title_single_nickname {
  font-size: 1rem;
  color: #000000;
  text-align: left;
  margin-bottom: 0.3rem;
  letter-spacing: 0.08rem;
}
.fans {
  font-size: 0.7rem;
  color: #aaaaaa;
  text-align: left;
}
.concern {
  width: 3rem;
  height: 1.5rem;
  border: 1px solid #0ac0f6;
  border-radius: 3rem;
  padding: 0.1rem 0.6rem 0;
  font-size: 0.9rem;
  color: #0ac0f6;
}
.single_intro {
  width: 100%;
  /* font-size: 0.7rem; */
  color: #f65150;
  text-align: left;
  display: flex;
  -webkit-transform-origin-x: 0;
  -webkit-transform: scale(0.6);
}
.single_intro span {
  background: #fee8ea;
  border-radius: 0.8rem;
  padding: 0 0.4rem;
  margin-right: 0.2rem;
}
.expert_intro {
  font-size: 0.7rem;
  color: #989898;
  text-align: left;
  margin-top: 0.3rem;
}
/* 近期战绩 */
.recent_achievements {
  border-bottom: 0.5rem solid #f1f1f1;
}
.type_name {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #f2f2f2;
}
.type_name li {
  color: #000000;
  font-size: 0.9rem;
  width: 5rem;
  text-align: center;
  margin-left: 0.4rem;
  padding-top: 0.6rem;
  padding-bottom: 0.6rem;
}
.type_name li:first-child {
  color: #17b732;
  border-bottom: 2px solid #14b33a;
  font-weight: 700;
}
.type_trend {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.6rem 0.8rem;
  border-bottom: 1px solid #f2f2f2;
}
.trend_title {
  color: #000000;
}
.all_match {
  border: none;
  border: 1px solid #cccccc;
  border-radius: 2rem;
  font-size: 0.7rem;
  padding-bottom: 0.2rem;
  padding: 0rem 0.1rem 0.2rem 0.4rem;
  color: #424242;
  outline: none;
}
.trend_chart {
  height: 10.6rem;
  background: #fafafa;
}
.trend_img {
  width: 100%;
  margin-top: 0.2rem;
}
/* 在售方案 */
.onsale_scheme {
  border-bottom: 0.5rem solid #f1f1f1;
}
.scheme_title {
  padding: 1rem 0.7rem 0.6rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ececec;
}
.scheme_title_left {
  display: flex;
  font-size: 1.1rem;
  height: 1rem;
  line-height: 1rem;
  /* font-family: 宋体; */
}
.scheme_title_right {
  font-size: 0.7rem;
  color: #a3a3a3;
}
.scheme_detail {
  padding: 0 0.8rem 0.8rem;
  border-bottom: 1px solid #eeeeee;
}
.team_name {
  font-size: 1rem;
  margin-top: 1rem;
  text-align: left;
  line-height: 1.7rem;
}
.season {
  background: #f4f4f4;
  display: flex;
  justify-content: space-between;
  border-radius: 1rem;
  padding: 0.3rem 0.7rem;
  margin-top: 1rem;
}
.season_title {
  font-size: 0.7rem;
  color: #a2a2a2;
}
.right_row {
  color: #a2a2a2;
}
.time_zuan {
  display: flex;
  justify-content: space-between;
  font-size: 0.7rem;
  margin-top: 1rem;
}
.time {
  color: #a1a1a1;
}
.zuan {
  color: #d3ae74;
}
/* 历史方案 */
.history_scheme {
  border: 0;
}
.red {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  background: #f52928;
  color: #f5b6b2;
}
.black {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  background: #000000;
  color: #edfafe;
}
</style>


