    border-radius: 0.5rem;
    font-size: 0.7rem;
    color: #ffffff;
    padding: 0.3rem 0;
  }
  .result_red p{
    background: #F22C2D;
    border-radius: 0.5rem;
    font-size: 0.7rem;
