  }
  .lost_blue{
    color: #23A4F8;
  }
  .jinnang{
    width: 96%;
    padding: 0.8rem 0.5rem 0.6rem;
  }
  .Injury_detail_title{
    background: #ECECEC;
  }
  .six_clash_title{
