    color: #39C4FA;
    border-radius: 1rem;
  }
  .expert_forecast_type .type1{
    border-right: 1px solid #03BEFA;
    padding: 0.2rem 0.8rem;
  }
  .expert_forecast_type .type1:last-child{
    border-right: none;
  }
  
  .expert_forecast_type .type1:first-child{
