/* 指数页面 */
  .index_number{
    margin-top: 1rem;
    font-size: 0.7rem;
    background: #ffffff;
  }
  .lottery_title{
    background: #F5F5F5;
    color: #A9A9A9;
  }
  .company{
    border-right: 1px solid #EBEBEB;
  }
  .lottery_name{
    width: 4rem;
    background: #E7F7EC;
    border-right: 1px solid #EFF0ED;
  }
  tr:nth-child(2n){
    background: #F5F5F5;
  }
  .color_green{
    color: #30BA53;
  }
  .color_red{
    color: #E67160;
  }
  tr td:nth-child(4){
    border-right: 1px solid #E8E8E8;
  }
  .td1{
    border-right: 1px solid #E5E5E5;
  }
  .td2{
    border-right: none;
  }
</style>


