<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback">
      <div class="title"></div>
      <div class="release"></div>
    </header>
    <router-link class="fragment_bg" :to="{name:'/personal/activity_rules'}">
      <div class="activity_rules">活动规则</div>
    </router-link>
    <div class="fragment_num1">3</div>
    <div class="fragment_num2">4</div>
  </div>
</template>
<script>
export default {
  methods:{
    goback(){
      window.history.back(-1);
    },
  }
}
</script>
<style scoped>
header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    font-weight: bold;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
  }
  .release{
    width: 0.5rem;
    height: 0.5rem;
  }
  .fragment_bg{
    width: 100%;
    height: 700px;
    background: url('../../assets/personal/suipian_bg.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    
  }
  .activity_rules{
    width: 4rem;
    height: 1rem;
    background: #FB8E0A;
    color: #FEF9F1;
    font-size: 0.7rem;
    padding: 0.25rem 0;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    position: absolute;
    right: 0;
    top: 1rem;
  }
  .fragment_num1{
    width: 1.7rem;
    height: 1.7rem;
    background: #FFDE00;
    border-radius: 3px;
    line-height: 1.7rem;
    color: #D80C00;
    font-size: 1.1rem;
    margin-top: -21.2rem;
    margin-left: 57%;
  }
  .fragment_num2{
    width: 1.7rem;
    height: 1.7rem;
    background: #FFDE00;
    border-radius: 3px;
    line-height: 1.7rem;
    color: #D80C00;
    font-size: 1.1rem;
    margin-top: 17.3rem;
    margin-left: 57%;
  }
</style>


