<script>
import playerList from '../../components/Player_list'
import teamList from '../../components/Team_list'
import schedule from '../../components/Football_schedule'

import qs from 'qs'
export default {
  data() {
    return {
      active: 0,
      currentTab: 'integralList',
      leagueNav:[],
      leagueList:[]
    };
  },
  components: { 
    playerList,
    teamList,
    schedule,
   
  },
  mounted() {
    this.leagueNames(); 
    this.integralList();
  },
  methods: {
    toggleTab: function(tab) {
      this.currentTab = tab; 
    },
    // 联赛导航栏
    leagueNames(){
      const data = {
        _uid: this.Sign.uid,
        _sign: this.Sign.sign
      };
      this.$axios
        .post(
          "http://47.98.97.133/index.php?g=Cliapi&m=Match&a=LeagueData",
          qs.stringify(data)
        )
        .then(res => {
          // console.log(res.data.data);
          if (res.data.status == 1) {
            this.leagueNav = res.data.data[0];
              
          } else {
            console.log(res.data.info);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 联赛积分榜
    integralList(){
      const data = {
        _uid: this.Sign.uid,
        _sign: this.Sign.sign,
        leagueId : 3
      };
      this.$axios
        .post(
          "http://47.98.97.133/index.php?g=Cliapi&m=DataList&a=byleagueIdGetTeam",
          qs.stringify(data)
        )
        .then(res => {
          console.log(res.data);
          if (res.data.status == 1) {
            this.leagueList = res.data.data;            
          } else {
            console.log(res.data.info);            
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
}
</script>
<style scoped>
