      1.2为提高用户的使用感受和满意度，用户同意本应用将基于用户的操作行为对用户数据进行调查研究和分析，从而进一步优化服务。

      2.服务内容

      2.1平台内严禁一切非法、涉黄信息，违反社区运营规范者，一律封号处理。

