</template>
<script>
import qs from 'qs'
export default {
  data(){
    return {
      pagedata:{}
    }
  },
  mounted(){
    this.getpageData();
  },
  methods:{
    async getpageData(){
      let data = {}
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Banner&a=agreement",qs.stringify(data))
        
        if(result.data.status==1){
          
          this.pagedata = result.data.data
        }
      }catch(err){
        console.log(err);
      }
    },
    goback(){
      window.history.back(-1);
    }
  }
}
</script>
<style scoped>
  .title{
    color: #333333;
  }
  .agreement{
    font-size: 0.8rem;
    padding: 0.5rem 0.7rem;
    line-height: 1.5rem;
    text-align: left;
    text-indent: 1.5rem;
  }
</style>
