    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
  .collect_content p{
    margin-bottom: 1rem;
  }
  .collect_content p.bold{
    font-weight: bold;
    margin-bottom: 0.5rem;
  }

</style>
<style>
.collect_content-html{
    padding: 1rem;
    font-size: 0.9rem;
    text-align: left;
    color: #333;
    line-height: 1.1rem;
  }
</style>


