<template>
  <div>
    <header>
      <img src="../../assets/home/fanhui.png" alt="" class="goback" @click="goback()">
      <div class="title">资讯详情</div>
      <div class="release"></div>
    </header>
    <div class="news">
        <div class="news_title">{{pageData.title}}</div>
        <div class="author_time">
            <div class="author_time_left">
              <span class="news_author">{{pageData.uname}}</span>
              <span class="news_time">{{pageData.addtime}}</span>
            </div>
            <div class="author_time_right">
                <img src="../../assets/home/renzheng@2x.png" class="renzheng_img">
                <p class="renzheng">官方认证</p>
            </div>
        </div>
        <div class="news_content" v-html="pageData.content">
          </div>
    </div>
    <!-- 精彩评论 -->
    <div class="good_comment">
      <div class="good_comment_title">
        <p class="small_line"></p>
        <strong class="">精彩评论</strong>
      </div>
      <div class="expert_info">
        <div class="expert_info_one" v-for="(item,index) of commentsList" :key="index">
          <div class="expert_info_one_top">
            <div class="head_title">
              <img :src="item.avatar" class="head">
              <div class="title_single">
                <div class="title_single_nickname">{{item.nickname}}</div>
                <div class="fans">{{item.time}}</div>
              </div>
            </div>
            <div class="concern">
              <p class="zan_num">{{item.zan || 0}}</p>
              <img src="../../assets/home/no_zan@2x.png" class="no_zan">
            </div>
          </div>
          <div class="expert_intro">
            {{item.content}}
          </div> 
        </div>



        <!-- <div class="expert_info_one">
          <div class="expert_info_one_top">
            <div class="head_title">
              <img src="../../assets/home/expert.jpg" class="head">
              <div class="title_single">
                <div class="title_single_nickname">米兰朱教练于谦</div>
                <div class="fans">40分钟前</div>
              </div>
            </div>
            <div class="concern">
              <p class="zan_num">43</p>
              <img src="../../assets/home/no_zan@2x.png" class="no_zan">
            </div>
          </div>
          <div class="expert_intro">
            前中国足球运动员，出生于山东省青岛市，已退役。中国国家队历史射手榜第一名。参加过2002年韩日世界杯以及1992年、1996年、2004年三届亚洲杯。
          </div> 
        </div>
        <div class="expert_info_one">
          <div class="expert_info_one_top">
            <div class="head_title">
              <img src="../../assets/home/expert.jpg" class="head">
              <div class="title_single">
                <div class="title_single_nickname">蓝黑1986</div>
                <div class="fans">43分钟前</div>
              </div>
            </div>
            <div class="concern">
              <p class="zan_num">25</p>
              <img src="../../assets/home/zan.png" class="no_zan">
            </div>
          </div>
          <div class="expert_intro">
            前中国足球运动员，出生于山东省青岛市，已退役。中国国家队历史射手榜第一名。参加过2002年韩日世界杯以及1992年、1996年、2004年三届亚洲杯。
          </div> 
        </div> -->
      </div>
    </div>
    <!-- 发布评论 -->
    <div class="release_comment">
      <input type="text" class="ipt" placeholder="发表你的评论" v-model="contentstr" @blur="addComment">
      <img :src="pageData.praise == 1? zanTrue :zanFlase" alt="" @click="giveLike('praise')">
      <img :src="pageData.favor == 1? favorTrue :favorFalse" alt=""  @click="giveLike('favor')">
      <img src="../../assets/home/share.png" alt="">
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data(){
    return{
      pageData: {},
      id: '',
      contentstr:'',
      commentsList:[],
      zanTrue:require('../../assets/home/dianzan.png'),
      zanFlase:require('../../assets/home/weidianzan.png'),
      favorTrue:require('../../assets/home/collected@2x.png'),
      favorFalse:require('../../assets/home/collection.png')
    }
  },
  mounted() {
    this.id = this.$route.query.id || '';
    this.getPageData();
    this.getCommentsList();
  },
  methods: {
    async getPageData (){
      let data ={
        show_id: this.id
      }
      try{
        let result =await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Zixun&a=zixunDetail",qs.stringify(data))
        this.pageData = {};
        if(result.data.status==1){
          this.pageData = result.data.data;
          console.log(this.pageData)
        }
      }catch(err){
        this.pageData = {};
        console.log(err);
      }
    },
    // 获取评论列表
    async getCommentsList () {
      let data ={
        show_id: this.id,
        _uid: this.Sign.uid,
        _sign: this.Sign.sign,
      }
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Show&a=getCommlist",qs.stringify(data))
        
        if(result.data.status==1){
          this.commentsList = result.data.data;
          console.log(result.data)
        }
      }catch(err){
        console.log(err);
      }
    },
    // 发表评论
    async addComment () {
      let data ={
        show_id:this.id,
        _uid:this.Sign.uid,
        _sign:this.Sign.sign,
        content:this.contentstr
      }
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Show&a=addComment",qs.stringify(data))
        
        if(result.data.status==1){
          
          this.getCommentsList();
        }
      }catch(err){
        console.log(err);
      }
    },
    // 文章点赞收藏
    async giveLike (params) {
      let data ={
        id:this.id,
        _uid:this.Sign.uid,
        _sign:this.Sign.sign,
        type:params
      }
      try{
        let result = await this.$axios.post(this.Sign.baseUrl+"/index.php?g=Cliapi&m=Show&a=setShow",qs.stringify(data))
        
        if(result.data.status==1){
          // this.$toast(result.data.info);
          this.getPageData();
        }
      }catch(err){
        console.log(err);
      }
    },
    //返回
    goback(){
      this.$router.back(-1);
    }
  },
}
</script>
<style scoped>
  header{
    background: #00BFFE;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid #f00; */
    height: 2.4rem;
    line-height: 3.4rem;
    color: #ffffff;
  }
  .goback{
    width: 0.5rem;
    height: 1rem;
    color: #D8F2FE;
    padding-top: 1.2rem;
  }
  .title{
    font-size: 1.1rem;
    letter-spacing: 0.2rem;
    color: #EDFAFE;
    font-family: '微软雅黑';
    /* font-weight: 550; */
  }
  .news{
    padding: 0.6rem 0.8rem;
  }
  .news_title{
    font-size: 1.3rem;
    text-align: left;
    letter-spacing: 0.1rem;
    line-height: 2rem;
  }
  .author_time{
    display: flex;
    justify-content: space-between;
    margin-top: 0.7rem;
    margin-bottom: 0.7rem;
  }
  .author_time_left{
    display: flex;
    align-items: center;
  }
  .author_time_right{
    display: flex;
  }
  .news_author{
    color: #01C0FF;
    font-size: 0.8rem;
  }
  .news_time{
    color: #8F9497;
    font-size: 0.8rem;
    margin-left: 0.3rem;
  }
  .renzheng_img{
    width: 1rem;
    height: 1rem;
  }
  .renzheng{
    font-size: 0.7rem;
    color: #9B9B9B;
    margin-left: 0.3rem;
  }
  .news_content{
    text-align: left;
    line-height: 1.7rem;
    letter-spacing: 0.05rem;
    text-indent: 2.1rem;
  }

  .news_content img{
    width: 100%;
    margin-top: 0.4rem;
    margin-bottom: 0.2rem;
  }
  .news_content p{
    width: 100%;
  }
  .news_content p img{
    display: block;
    width: 100%;
    margin-top: 0.4rem;
    margin-bottom: 0.2rem;
    float: left;
  }
  .good_comment_title{
    font-size: 0.9rem;
    text-align: left;
    display: flex;
    padding: 0.6rem 0.8rem 0.2rem;
  }
  .small_line{
    width: 0.26rem;
    height: 1.1rem;
    background: #01BEFB;
    margin-right: 0.3rem;
    margin-top: 0.1rem;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
  }
  .expert_info_one{
    padding: 0.7rem 0.8rem 0.7rem;
    border-bottom: 2px solid #F1F1F1;
  }
  .expert_info_one_top{
    display: flex;
    justify-content: space-between;
  }
  .head_title{
    display: flex;
  }
  .head{
    width: 2.1rem;
    height: 2.1rem;
    border-radius: 50%;
  }
  .title_single{
    margin-left: 0.3rem;
  }
  .title_single_nickname{
    font-size: 0.8rem;
    color: #01BFFC;
    text-align: left;
  }
  .fans{
    -webkit-transform: scale(0.6);
    color: #AAAAAA;
    text-align: left;
    margin-left: -1rem;
  }
  .concern{
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  .zan_num{
    font-size: 0.7rem;
    color: #A1A1A1;
    margin-right: 0.4rem;
  }
  .no_zan{
    width: 0.85rem;
    height: 0.85rem;
  }
  .expert_intro{
    font-size: 0.9rem;
    color: #000000;
    text-align: left;
    margin-top: 0.3rem;
  }
  .release_comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #E7E7E7;
    padding: 0.8rem 0.8rem;
    
  }
  .ipt{
    background: #ffffff;
    width: 60%;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    border-radius: 0.5rem;
    padding-left: 0.7rem;
  }
  .ipt::placeholder{
    font-size: 0.8rem;
    letter-spacing: 0.08rem;
    color: #C1C1C1;
  }
  .release_comment img{
    width: 1.3rem;
    height: 1.3rem;
  }
</style>
<style>
.news_content img{
    width: 100%;
    margin-top: 0.4rem;
    margin-bottom: 0.2rem;
  }
  .news_content p{
    width: 100%;
  }
  .news_content p img{
    display: block;
    width: 100%;
    margin-top: 0.4rem;
    margin-bottom: 0.2rem;
    float: left;
  }
</style>



